@import url("https://fonts.googleapis.com/css?family=Arimo");
/* ======== */
/*   HTML   */
/* ========= */
html {
  font-size: 18px; }

/* ======== */
/*   BODY   */
/* ========= */
body {
  margin: 0;
  padding: 0;
  font-family: "Arimo", sans-serif;
  color: #333; }

h1,
h2,
h3,
h4,
h5 {
  color: #487ed6; }

/* =========== */
/*   HEADERS   */
/* =========== */
h1 {
  font-size: 3rem; }

h1.hero {
  font-size: 5rem; }

h2 {
  font-size: 2.25rem; }

h2.hero {
  font-size: 5rem; }

h3 {
  font-size: 1.5rem; }

h4 {
  font-size: 1rem; }

h5 {
  font-size: 0.875rem; }

